@import '../../modal/css/global';

.notifications-loader {
    @include pnp-svg-icon-classes;
    @include pnp-button;
}

.notifications-wrapper {
    @include pnp-svg-icon-classes;

    &__action {
        @include pnp-button;
        position: relative;
    }

    &__unread {
        @include pnp-typeface(text-14, no-margin);
        position: absolute;
        top: 6px;
        right: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 16px;
        height: 16px;
        color: $black;
        background: $utility-light;
        border-radius: 16px;
    }
}

.notification-popover {
    &__dialog {
        z-index: $popover;

        @include pnp-media-breakpoint-down(md) {
            left: 0 !important;
        }
    }
}

.notifications {
    @include pnp-svg-icon-classes;
    @include pnp-typeface(text-14, no-margin);
    @include pnp-standard-box-shadow;

    width: 375px;
    background: $white;
    border: 1px solid $grey-20;

    @include pnp-media-breakpoint-down(md) {
        width: calc(100vw - 24px);
        margin: 0 12px;
    }

    &__header {
        border-bottom: 1px solid $grey-20;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-18-bold, no-margin);
        padding: 20px;
    }

    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-height: calc(var(--popover-maxHeight) - 106px);
        padding-bottom: 20px;
        overflow: scroll;

        @include pnp-media-breakpoint-down(md) {
            max-height: calc(var(--popover-maxHeight) - 106px);
        }
    }

    &__list {
        @include pnp-list-reset;
        width: calc(100% - 25px);
        margin-right: 25px;
        padding: 20px;
        padding-bottom: 0;
    }

    &__expand-pinned,
    &__more {
        @include pnp-button;
        @include pnp-typeface(text-14-bold, no-margin);
    }

    &__expand-pinned {
        color: $black;
        background: transparent;
        border: unset;

        &:hover,
        &:focus {
            color: $black;
            background: $grey-10;
        }
    }
}

.notification-item {
    &__body {
        position: relative;
    }

    &__pinned,
    &__distance {
        margin-bottom: 8px;
    }

    &__icon {
        @include pnp-reset-button;
        position: absolute;
        top: 0;
        right: -35px;
        color: $grey-20;

        &--pinned {
            color: $primary-a;
        }
    }
}

.notification-content {
    margin-bottom: 16px;
    padding: 16px;
    word-break: break-word;
    background: $grey-10;

    &--unread {
        background: rgba($primary-a, 0.2);
        border: 1px solid $primary-a;
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 12px;
    }

    .svg-icon {
        margin-right: 8px;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(text-16-bold, no-margin);
    }

    &__subtitle {
        @include pnp-typeface(text-14-bold, no-margin);
        margin-bottom: 4px;
    }

    &__description {
        @include pnp-typeface(text-14, no-margin);
    }
}

.notification-content-modal {
    @include pnp-svg-icon-classes;
    @include pnp-typeface(text-14, no-margin);
    min-width: 300px;
    max-width: 400px;
    padding-bottom: 24px;
    word-break: break-word;

    &__header {
        display: flex;
        align-items: center;
        padding: 24px;
        padding-bottom: 16px;
    }

    .svg-icon {
        margin-right: 8px;
    }

    .svg-icon--small {
        margin-right: 6px;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-20-bold, no-margin);
    }

    &__body {
        padding: 0 24px;
    }

    &__date-distance {
        @include pnp-typeface(text-14-bold, no-margin);
        margin-bottom: 8px;
        color: $grey-70;
    }

    &__subtitle {
        @include pnp-typeface(text-14-bold, no-margin);
        margin-bottom: 4px;
    }

    &__link {
        display: block;
        margin-top: 12px;
        color: $grey-70;
        text-decoration: none;

        &:focus,
        &:hover {
            text-decoration: underline;
        }
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
        padding: 24px;
        padding-bottom: 0;
    }

    &__pin {
        @include pnp-button;
        @include pnp-typeface(text-14, no-margin);
        display: flex;
        align-items: center;
    }
}
